.clients {
  padding-top: 100px;
  padding-bottom: 106px;
  background: #F8F9FB;
}

.clients__title {
  margin-bottom: 65px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 40px;
  line-height: 65px;
  color: #000;
}

.clients__container {
  display: flex;
  flex-wrap: wrap;
}

.clients__container-slider {
  display: none;
}

.clients__slide {
  display: flex !important;
  flex-wrap: wrap;
  margin-bottom: 67px;
}

.clients__slide:last-child {
  display: none;
}

.clients__slide .clients__item:nth-child(2),
.clients__slide .clients__item:nth-child(5) {
  justify-content: center;
}

.clients__slide .clients__item:nth-child(3n) {
  justify-content: flex-end;
}

.clients__slide .clients__item:nth-child(4) {
  justify-content: flex-start;
}

.clients__item {
  display: flex;
  align-items: center;
  flex: 20%;
}

.clients__item:nth-child(-n+5) {
  margin-bottom: 69px;
}

.clients__item:nth-child(3),
.clients__item:nth-child(4),
.clients__item:nth-child(8),
.clients__item:nth-child(9){
  justify-content: center;
}

.clients__item:nth-child(5n) {
  justify-content: flex-end;
}

picture img{
  width: 100%;
}

@media (max-width: 1533px) {
  .clients {
    padding-top: 70px;
    padding-bottom: 70px;
    background: #F8F9FB;
  }

  .clients__title {
    margin-bottom: 50px;
    font-size: 26px;
    line-height: 34px;
  }

  .clients__item {
    display: flex;
    align-items: center;
    flex: 19%;
  }

  .clients__item:nth-child(-n+5) {
    margin-bottom: 36px;
  }
}

@media (max-width: 912px) {
  .clients {
    text-align: center;
    padding-bottom: 49px;
  }

  .clients__title {
    margin-bottom: 9px;
    font-size: 20px;
    line-height: 28px;
  }

  .clients__container {
    display: none;
  }

  .clients__container-slider {
    display: block;
  }

  .clients__slide {
    margin-bottom: 3px;
  }

  .clients__slide .clients__item {
    flex: 33%;
    height: 160px;
    margin-bottom: 0;
    align-items: center;
  }
}

@media (max-width: 420px) {
  .clients__title {
    max-width: 239px;
    margin: 0 auto;
    margin-bottom: 9px;
  }

  .clisets__slide .clients__item {
    align-items: flex-start;
    justify-content: flex-start;
  }

  .clients__slide .clients__item {
    flex: 50%;
  }

  .clients__slide .clients__item:nth-child(2n + 1) {
    justify-content: flex-start;
  }

  .clients__slide .clients__item:nth-child(2n) {
    justify-content: center;
  }
}