.wrapper {
  max-width: 1540px;
  margin: 0 auto;
}

.wrapper_large {
  max-width: 1800px;
  margin: 0 auto;
}

@media (max-width: 1800px) {
  .wrapper {
    margin: 0 100px;
  }
  
  .wrapper_large {
    margin: 0 30px;
  }
}

@media (max-width: 1024px) {
  .wrapper_large {
    margin: 0 20px;
  }
}

@media (max-width: 600px) {
  .wrapper {
    margin: 0 20px;
  }
}