.navigation {
  padding: 16px 0;
  background-color: #fff;
  border-bottom: 1px solid #EBEBEB;
}

.navigation__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation__logo {
  margin-right: 40px;
}

.navigation__container-flex {
  display: flex;
  align-items: center;
}

.navigation__links {
  display: flex;
  margin-right: 32px;
}

.navigation__links a {
  padding: 12px 20px;
  margin-right: 14px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #000;
  text-decoration: none;
  border-radius: 5px;
  transition: 0.2s ease color, 0.3s ease background-color;
}

.navigation__links a:hover {
  color: #fff;
  background-color: #66AC27;
  transition: 0.2s ease color, 0.3s ease background-color;
}

.navigation__links a:last-child {
  margin-right: 0;
}

.navigation__lc {
  display: inline-flex;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  line-height: 24px;
  color: #000;
  text-decoration: none;
  transition: 0.3s ease color;
}

.navigation__lc:hover {
  color: #66AC27;
  transition: 0.3s ease color;
}

.navigation__lc svg path {
  transition: 0.3s ease fill;
}

.navigation__lc:hover svg path {
  fill: #66AC27;
  transition: 0.3s ease fill;
}

.navigation__lc svg {
  margin-right: 10px;
}

.navigation__burger-btn {
  position: relative;
  display: none;
  width: 25px;
  height: 14px;
  background-color: transparent;
  border-bottom: 2px solid #1D1D1D;
  cursor: pointer;
}

.navigation__burger-btn:after {
  content: '';
  position: absolute;
  top: 6px;
  width: 25px;
  height: 2px;
  background-color: #1D1D1D;
}

.navigation__burger-btn:before {
  content: '';
  position: absolute;
  top: 0;
  width: 25px;
  height: 2px;
  background-color: #1D1D1D;
}

@media (max-width: 1470px) {
  .navigation {
    padding: 11px 0;
  }

  .navigation__logo {
    margin-right: 25px;
  }

  .navigation__links {
    margin-right: 25px;
  }

  .navigation__links a {
    padding: 6px 10px;
    margin-right: 13px;
  }
}

@media (max-width: 1024px) {

  .navigation__links {
    margin-right: 17px;
  }

  .navigation__links a {
    padding: 6px 10px;
    margin-right: 5px;
  }
}

@media (max-width: 963px) {
  .navigation {
    display: flex;
    justify-content: flex-end;
    padding: 13px 0;
  }

  .navigation__container {
    display: none;
  }

  .navigation__burger-btn {
    display: block;
  }
}