.track {
  margin-bottom: 110px;
}

.track__container {
  position: relative;
  display: flex;
  justify-content: flex-end;
  padding: 78px 0;
  background-color: #F3F3F5;
  border-radius: 14px;
  overflow: hidden;
}

.track__letter-bg {
  position: absolute;
  top: 61px;
  left: -108px;
  width: 773px;
  height: 773px;
  background: #66AC27;
  border-radius: 50%;
}

.track__letter {
  position: absolute;
  top: 60px;
  left: 138px;
}

.track__content {
  margin-right: 156px;
}

.track__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 65px;
  color: #000;
}

.track__sub-title {
  margin-bottom: 30px;
  font-family: OpenSans, serif;
  font-size: 18px;
  line-height: 28px;
  color: #101010;
}

.track__trace-form {
  margin-bottom: 42px;
}

.track__mobile {
  max-width: 491px;
  margin-bottom: 20px;
  font-family: OpenSans, serif;
  font-size: 16px;
  line-height: 26px;
  color: #101010;
  opacity: 0.5;
}

.track__mobile-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 182px;
  height: 52px;
  background-color: #F9F9FA;
  border: 1px solid #000000;
  border-radius: 4px;
  transition: 0.3s ease background-color;
}

.track__mobile-btn:hover {
  background-color: #000;
  transition: 0.3s ease background-color;
}

.track__mobile-btn svg path {
  transition: 0.3s ease fill;
}

.track__mobile-btn:hover svg path {
  fill: #fff;
  transition: 0.3s ease fill;
}

.track__mobile-btn:first-child {
  margin-right: 21px;
}

@media (max-width: 1630px) {
  .track__container {
    padding: 34px 0;
  }

  .track__content {
    margin-left: 363px;
    margin-right: 37px;
  }

  .track__letter {
    width: 246px;
    height: 183px;
    top: 96px;
    left: 153px;
  }

  .track__letter-bg {
    left: -133px;
    width: 438px;
    height: 438px;
  }

  .track__trace-form {
    margin-bottom: 24px;
  }

  .track__mobile {
    margin-bottom: 16px;
    font-size: 15px;
    line-height: 24px;
  }

  .track__title {
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 32px;
  }

  .track__sub-title {
    margin-bottom: 22px;
    font-size: 16px;
    line-height: 24px;
  }
  
  .track__trace-form .trace__input {
    width: 352px;
    padding: 12px 12px;
  }

  .track__trace-form .trace__btn {
    width: 48px;
    height: 48px;
  }

  .track__trace-form .trace__btn span{
    display: none;
  }

  .track__trace-form .trace__loupe-svg {
    margin-right: 0;
  }

  .track__trace-form .trace__input::placeholder {
    font-size: 16px;
  }
}

@media (max-width: 990px) {
  .track {
    margin-bottom: 0;
  }

  .track__container {
    padding: 30px 0 230px 0;
    margin: 0;
    justify-content: center;
    border-radius: 0;
  }

  .track__title {
    margin-bottom: 5px;
    font-size: 20px;
    line-height: 34px;
  }

  .track__sub-title {
    margin-bottom: 32px;
    font-size: 14px;
    line-height: 22px;
  }

  .track__content {
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
  }

  .track__letter-bg {
    left: -169px;
    top: 298px;
    width: 445px;
    height: 445px;
  }
  
  .track__letter {
    width: 188px;
    height: 141px;
    top: 56px;
    left: 178px;
  }

  .track__trace-form .trace__input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 15px;
  }

  .track__trace-form .trace__input::placeholder {
    font-size: 12px;
  }

  .track__trace-form {
    flex-direction: column;
  }

  .track__trace-form .trace__btn {
    width: 100%;
    height: 48px;
  }

  .track__trace-form .trace__btn span{
    display: block;
  }

  .track__trace-form .trace__loupe-svg {
    margin-right: 10px;
  }

  .track__mobile {
    display: none;
  }

  .track__mobile-btn {
    display: none;
  }
}
