.header {
  padding-top: 108px;
  padding-bottom: 83px;
}

.header__title {
  max-width: 802px;
  margin-bottom: 18px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 55px;
  line-height: 65px;
  letter-spacing: -0.01em;
  color: #101010;
}

.header__title span {
  color: #66AC27;
}

.header__sub-title {
  margin-bottom: 45px;
  font-family: OpenSans, serif;
  font-size: 20px;
  line-height: 28px;
  color: #101010;
}

.header__btn {
  width: 281px;
  height: 48px;
  font-family: OpenSans, serif;
  font-size: 18px;
  line-height: 30px;
  color: #fff;
  background-color: #66AC27;
  border: none;
  border-radius: 5px;
  transition: 0.3s ease background-color;
}

.header__btn:hover {
  background-color: #7FD432;
  transition: 0.3s ease background-color;
}

@media (max-width: 1024px) {
  .header {
    padding-top: 72px;
    padding-bottom: 61px;
  }

  .header__title {
    margin-bottom: 12px;
    max-width: 547px;
    font-size: 38px;
    line-height: 44px;
  }

  .header__sub-title {
    margin-bottom: 34px;
    font-size: 16px;
  }

  .header__btn {
    width: 254px;
    height: 42px;
    font-size: 16px;
    line-height: 26px;
  }
}

@media (max-width: 552px) {
  .header {
    padding-top: 35px;
    padding-bottom: 35px;
  }
  .header__title {
    max-width: 278px;
    margin-bottom: 8px;
    font-size: 26px;
    line-height: 34px;
  }
  .header__sub-title {
    margin-bottom: 22px;
    font-size: 14px;
    line-height: 22px;
  }

  .header__btn {
    height: 48px;
  }
}

@media (max-width: 320px) {
  .header__btn {
    width: 100%;
    font-size: 14px;
    line-height: 30px;
  }
}