
.services {
  overflow: hidden;
}

.services__container-carousel {
  display: none;
  margin: 0 20px;
}

.services__item-container {
  display: flex;
}

.services__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex: 1;
  height: 440px;
  padding-left: 58px;
  padding-top: 192px;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  -webkit-user-drag: none;
  transition: 0.6s ease padding-top;
}

.services__item picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.services__item-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.services__item-title {
  max-width: 310px;
  margin-bottom: 10px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 30px;
  line-height: 38px;
  letter-spacing: -0.01em;
  color: #fff;
}

.services__item-title_width {
  max-width: 301px;
}

.services__item-sub-title {
  max-width: 294px;
  margin-bottom: 28px;
  font-family: OpenSans, serif;
  font-size: 18px;
  line-height: 26px;
  color: #fff;
}

.services__item-hidden {
  max-height: 0;
  overflow: hidden;
  transition: 0.6s ease max-height;
}

.services__item:hover {
  padding-top: 126px;
  transition: 0.6s ease padding-top;
}

.services__item:hover .services__item-hidden{
  max-height: 126px;
  transition: 0.6s ease max-height;
}

@media (max-width: 1565px) {
  .services__item {
    height: 256px;
    padding-left: 20px;
    padding-top: 110px;
  }

  .services__item-title {
    max-width: 310px;
    margin-bottom: 6px;
    font-size: 19px;
    line-height: 24px;
  }

  .services__item-sub-title {
    max-width: 200px;
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 22px;
  }

  .services__item:hover {
    padding-top: 45px;
  }
  
  .services__item:hover .services__item-hidden{
    max-height: 126px;
  }
}

@media (max-width: 930px) {
  .services__item {
    padding-top: 81px;
  }

  .services__item-title {
    font-size: 14px;
    line-height: 19px;
  }

  .services__item-sub-title {
    margin-bottom: 12px;
    font-size: 12px;
  }
}

.services .slick-list {
  height: 215px;
  margin: 0 -8px !important;
  overflow: visible !important;
}
/*slick*/
.services .slick-track {
  display: flex !important;
  position: absolute !important;
}

.services .slick-slide {
  margin: 0 8px;
  width: 280px !important;
}

.services .slick-slider {
  overflow: visible;
}
/*slick*/


@media (max-width: 790px) {
  .services__item {
    width: 280px;
    height: 215px;
    padding-left: 20px;
    padding-top: 110px;
    border-radius: 4px;
  }

  .services__item-container {
    display: none;
  }

  .services__container-carousel {
    display: block;
  }
}

@media (max-width: 440px) {
  .services__item {
    padding-top: 81px;
  }

  .services__item:hover {
    padding-top: 25px;
  }

  .services__item-title {
    font-size: 21px;
    line-height: 27px;
  }
}
