.footer {
  padding-top: 80px;
  background-color: #fff;
  overflow: hidden;
}

.footer__container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 69px;
}

.footer__nav-links {
  display: flex;
}

.footer__nav-links div:first-child {
  margin-right: 177px;
}

.footer__nav-links li {
  margin-bottom: 25px;
}

.footer__nav-links li:last-child {
  margin-bottom: 0;
}

.footer__link {
  display: block;
  min-width: 350px;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  font-weight: 300;
  line-height: 29px;
  text-decoration: none;
  color: #1B1B1F;
}

.footer__link:hover {
  color: #66AC27;
  transition: 0.3s ease color;
}

.footer__contacts {
  position: relative;
}

.footer__contacts::after {
  content: none;
  position: absolute;
  top: -30px;
  left: -110px;
  height: 1px;
  width: 100vw;
  background-color: #EBEBEB;
}

.footer__mobile {
  font-family: 'Montserrat', sans-serif;
  font-size: 26px;
  line-height: 31px;
  color: #1B1B1F;
}

.footer__mobile-desc {
  margin-bottom: 22px;
  font-family: OpenSans, sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 20px;
  color: #7B7B7D;
}

.footer__mail-contacts {
  margin-bottom: 18px;
}

.footer__mail-contacts:last-child {
  margin-bottom: 0;
}

.footer__mail-title {
  margin-bottom: 6px;
  font-family: OpenSans, sans-serif;
  font-weight: 300;
  font-size: 13px;
  line-height: 18px;
  color: #1B1B1F;
}

.footer__mail {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #1B1B1F;
}

.footer__copyright {
  padding: 22px 0 30px 0;
  border-top: 1px solid #EBEBEB;
}

.footer__copyright-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__copyright-text {
  font-family: OpenSans, sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  opacity: 0.6;
}


@media (max-width: 1310px) {
  .footer {
    padding-top: 64px;
  }

  .footer__container {
    margin-bottom: 52px;
  }

  .footer__nav-links div:first-child {
    margin-right: 76px;
  }

  .footer__nav-links li {
    margin-bottom: 22px;
  }

  .footer__link {
    font-size: 18px;
    line-height: 22px;
    min-width: unset;
  }

  .footer__mobile {
    font-size: 22px;
    line-height: 26px;
  }

  .footer__mobile-desc {
    margin-bottom: 18px;
    font-size: 14px;
    line-height: 19px;
  }

  .footer__mail-contacts {
    margin-bottom: 16px;
  }

  .footer__mail-title {
    margin-bottom: 3px;
    font-size: 12px;
    line-height: 16px;
  }

  .footer__mail {
    font-size: 14px;
    line-height: 17px;
  }

  .footer__copyright {
    padding: 18px 0 21px 0;
  }

  .footer__copyright-text {
    font-size: 12px;
  }
}

@media (max-width: 940px) {
  .footer {
    padding-top: 40px;
  }

  .footer__nav-links {
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 70px;
  }

  .footer__nav-links div:first-child {
    margin-right: 0;
    margin-bottom: 22px;
  }

  .footer__contacts::after {
    content: "";
  }

  .footer__container {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 30px;
  }

  .footer__copyright-text:last-child {
    display: none;
  }

  .footer__copyright-text {
    font-size: 11px;
  }

  .footer__copyright {
    padding: 15px 0 21px 0;
  }

  .footer__mobile {
    font-size: 24px;
    line-height: 29px;
  }

  .footer__mobile-desc {
    margin-bottom: 21px;
  }

  .footer__mail-title {
    margin-bottom: 8px;
  }

  .footer__mail {
    font-size: 16px;
    line-height: 19px;
  }
}

@media (max-width: 600px) {
  .footer__contacts::after {
    left: -25px;
  }
}
