.advantages {
  padding-top: 130px;
  margin-bottom: 110px;
  background-color: #fff;
}

.advantages__title {
  margin-bottom: 90px;
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  font-weight: 600;
  line-height: 65px;
  color: #000;
  text-align: center;
}

.advantages__container {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
}

.advantages__container-carousel {
  display: none;
  overflow: hidden;
}

.advantages__item {
  display: flex;
  align-items: center;
  padding: 37px;
  flex: 26%;
  margin-right: 20px;
  margin-bottom: 40px;
  background-color: #F8F9FB;
  border-radius: 14px;
}

.advantages__item:nth-last-child(-n+3) {
  margin-bottom: 0;
}

.advantages__item:nth-child(3n) {
  margin-right: 0;
}

.advantages__circle {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 59px;
  height: 59px;
  margin-right: 20px;
  border-radius: 50%;
  background-color: #66AC27;
}

.advantages__item-title {
  max-width: 324px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 21px;
  line-height: 27px;
  color: #000;
}


@media (max-width: 1375px) {
  .advantages {
    padding-top: 90px;
    margin-bottom: 70px;
  }

  .advantages__title {
    margin-bottom: 42px;
    font-size: 26px;
    line-height: 34px;
  }

  .advantages__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    padding: 28px 20px 28px 20px;
    margin-bottom: 20px;
  }
  
  .advantages__circle {
    margin-right: 0;
    margin-bottom: 10px;
    width: 49px;
    height: 49px;
  }

  .advantages__circle svg {
    width: 27px;
    height: 27px;
  }

  .advantages__item-title {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }
}

.advantages .slick-list {
  height: 173px;
  overflow: visible !important;
  margin: 0 -8px 25px -8px !important;
}

/*slick*/
.advantages .slick-track {
  display: flex !important;
  position: absolute !important;
}

.advantages .slick-slide {
  margin: 0 8px;
  
}

.advantages .slick-slider {
  overflow: visible;
}

.advantages .slick-dots {
  position: static !important;
}

.advantages .slick-dots li {
  width: 20px;
  margin: 0;
}

.advantages .slick-dots li button:before {
  opacity: 0.1;
}

.slick-dots li.slick-active button:before {
  font-size: 13px;
  opacity: 1;
  color: #66AC27;
}
/*slick*/

@media (max-width: 830px) {
  .advantages {
    padding-top: 50px;
    margin-bottom: 50px;
  }

  .advantages__title {
    margin-bottom: 30px;
    font-size: 20px;
  }

  .advantages__item {
    margin-right: 0;
  }

  .advantages__container {
    display: none;
  }

  .advantages__container-carousel {
    display: block;
  }

  .advantages__item {
    display: inline-flex !important;
    height: 173px;
  }
}

@media (max-width: 440px) {
  .advantages .slick-list {
    margin: 0 0px 19px 0px !important;
  }

  .advantages .slick-slide {
    margin: 0;
  }
}
