.trace {
    display: flex;
    align-items: center;
}

.trace__input {
    width: 352px;
    padding: 12px 20px;
    margin-right: 20px;
    font-family: OpenSans, serif;
    font-size: 16px;
    line-height: 14px;
    color: #000;
    background: #F8F9FA;
    border: 1px solid #BECAE2;
    border-radius: 5px;
    transition: 0.3s ease border-color;
}

.trace__input:focus {
    border-color: #000;
    transition: 0.3s ease border-color;
}

.trace__input:hover {
    border-color: #8D8D8D;
    transition: 0.3s ease border-color;
}

.trace__input::placeholder {
    font-family: OpenSans, serif;
    font-size: 16px;
    line-height: 14px;
    color: #ABB7C7;
}

.trace__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 213px;
    height: 48px;
    font-family: OpenSans, serif;
    font-size: 16px;
    line-height: 30px;
    color: #66AC27;
    background: transparent;
    border: 1.5px solid #66AC27;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s ease border-color, 0.3s ease color;
}
    
.trace__btn:hover {
    color: #7FD432;
    border-color: #7FD432;
    transition: 0.3s ease border-color, 0.3s ease color;
}

.trace__btn svg path {
    transition: 0.3s ease fill;
}

.trace__btn:hover svg path {
    fill: #7FD432;
    transition: 0.3s ease fill;
}

.trace__loupe-svg {
    margin-right: 10px;
}

@media (max-width: 1470px) {
    .trace__input {
        width: 278px;
        padding: 6px 12px;
        margin-right: 10px;
    }
    
    .trace__input::placeholder {
        font-size: 13px;
    }

    .trace__btn {
        width: 36px;
        height: 36px;
    }

    .trace__btn span{
        display: none;
    }

    .trace__loupe-svg {
        margin-right: 0;
    }
}